import './DeliveryMap.css'

import { autorun, IReactionDisposer, reaction } from 'mobx'
import React from 'react'
import { Marker } from 'react-google-maps'

import { latLngToGps } from '../../helpers/gps'
import { TRootModel } from '../../models/RootModel'
import { AddressInput } from '../destination/AddressInput'
import { AddressSubmit } from './AddressSubmit'

interface IProps {
  rootModel: TRootModel
  onConfirm(): void
}

export class DeliveryMap extends React.Component<IProps> {
  disposers: IReactionDisposer[] = []
  componentDidMount() {
    const {
      rootModel: { location, map },
    } = this.props

    this.disposers = [
      reaction(
        () => map.clickPoint,
        clickPoint => {
          if (clickPoint) {
            location.withUserLocation(clickPoint)
            map.panToLocation(clickPoint)
          }
        },
      ),

      autorun(() => {
        if (map.isReady && location.hasLocation) {
          map.panToLocation(location.geoPoint)
        }
      }),
    ]
  }
  componentWillUnmount() {
    this.disposers.forEach(dis => dis())
  }
  render() {
    const {
      onConfirm,
      rootModel: { location, order },
    } = this.props
    return (
      <>
        <Marker
          position={location.latLng}
          defaultDraggable={true}
          onDragEnd={({ latLng }) => {
            location.withUserLocation(latLngToGps(latLng))
          }}
        />
        <div className="delivery__map__address">
          <AddressInput />
        </div>
        <div className="delivery__map__submit">
          <AddressSubmit
            enabled={location.isDeliveryAddress}
            onClick={() => {
              order.setBranch(location.branchId!)
              order.createDeliveryOrder(onConfirm)
            }}
            onPrefetch={() => order.prefetchFoodMenu(location.branchId!)}
          />
        </div>
      </>
    )
  }
}
