import './AddressInput.css'

import { Trans } from '@lingui/react'
import React from 'react'
import { Alert, InputGroup } from 'reactstrap'
import styled from 'styled-components'

import { WithRoot } from '../../common/WithRoot'
import { i18n } from '../../i18n'
import { TLocationModel } from '../../models/LocationModel'
import GeolocationButton from './GeolocationButton'
import ResolvedAddress from './ResolvedAddress'
import SearchInput from './SearchInput'

const AddressInputStyled = styled.div`
  width: 90%;
  min-width: 14rem;
  margin: 1rem auto;
  text-align: center;
`

const AddressInputBoxStyled = styled.div`
  border-radius: 1rem;
  border: 1px solid #d4d4d4;
  background-color: white;
  height: 2.3rem;
  overflow: hidden;
  & input {
    margin: 0.2rem 2rem 0.5rem 0.7rem;
    background: transparent;
    border: none;
  }
`

export const AddressInput: React.SFC = () => (
  <WithRoot
    observe={true}
    render={({ location }) => (
      <AddressInputStyled>
        <AddressInputBoxStyled>
          <InputGroup size="sm">
            <ResolvedAddress
              address={location.address || ''}
              isVisible={location.hasAddress}
              isResolving={location.isResolving}
              onValidClick={location.clearAddress}
            />
            <SearchInput
              inputProps={{
                placeholder: i18n.t`Type address or choose it from map`,
                autoComplete: 'shipping street-address',
              }}
              onSearch={location.withUserAddress}
              isVisible={!location.hasAddress}
              isSearching={location.isResolving}
            />
            <GeolocationButton
              onClick={location.attemptGeolocation}
              isVisible={
                location.isGeolocationAvailable && !location.isResolving
              }
            />
          </InputGroup>
        </AddressInputBoxStyled>
        {location.hasFinishedSearch ? renderErrorMessage(location) : null}
      </AddressInputStyled>
    )}
  />
)

const NoMouseAlert = styled(Alert)`
  pointer-events: none;
`

function renderErrorMessage(location: TLocationModel) {
  if (location.isResolved === false) {
    return (
      <NoMouseAlert color="danger">
        <Trans>Address was not recognized.</Trans>
      </NoMouseAlert>
    )
  }
  if (location.isStreetAddress === false) {
    return (
      <NoMouseAlert color="warning">
        <Trans>Please enter address with house number.</Trans>
      </NoMouseAlert>
    )
  }
  if (location.inDeliveryLocation === false) {
    return (
      <NoMouseAlert color="warning">
        <Trans>
          Meal can't be delivered to your chosen address by any restaurant.
        </Trans>
      </NoMouseAlert>
    )
  }
  return null
}
